body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper{
  background: blanchedalmond;
  padding: 20px;
  border-radius:2%;
  width:500px;
  margin:30px;
    
}

.Input-wrapper input{
  border-radius:4% ;
  border:0; 
  width: 400px;
  height:25px;
  align-items: center;
  padding: 6px;
}

.Input-wrapper button {
  width:45px;
  height:25px;
  padding: 0;
  align-items:center;
  background: rgb(226, 147, 43);
  border-radius: 3%;
  }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}


.item{
  display:flex;
  justify-content: space-between;
  align-items: center;
  margin:10px;
  width: 250px;
  height: 20px;
  border:1px solid red;
  padding:10px;
  background: rgb(112, 26, 62);
   border-radius: 4%;
   margin:10px;
    
}
 .item p {
  margin:0;
  font-family: "fantasy";
  font-size:18px;
  width:400px;
}

.item button {
  width:50px;
  height:20px;
  text-align: center;
  padding:2px;
}

h1 {
 padding:10px;
 margin:10px;
 
}
